import { Route, Routes } from "react-router-dom";
import Login from "./pages/login/login";
import Market from "./pages/market/market";
import BookSpa from "./pages/bookSpa/bookSpa";
import Welcome from "./pages/Welcome/welcome";
import Upload from "./pages/uploadExcel/upload";
import Admin from "./pages/Admin/admin";
import BookTrip from "./pages/bookTrip/bookTrip";
import Souvenir from './pages/souvinier/souvenir';
import { Menubar } from 'primereact/menubar';

const items = [
  {label:"Booking", url: "/welcome"},
  {label:"Spa Massage", url: "/spa"},
  {label:"Souvenir Shop", url: "/souvenir"},
  {label:"Minimarket", url: "/market"},
  {label:"Trip", url: "/trip"}
];

const App = () => {

  return (

    <div>
      <Menubar model={items} />
      <Routes>
        <Route path="/"  element={<Login />}/>
        <Route path="/login" element={<Login />} />
        <Route path="/welcome" element={<Welcome />}  />
        <Route path="/trip" element={<BookTrip />} />
        <Route path="/souvenir" element={<Souvenir />} />
        <Route path="/spa" element={<BookSpa />} />
        <Route path="/admin/upload"  element={<Upload/>}/>
        <Route path="/market" element={<Market />} />
        <Route path="/admin" element={<Admin />} />
      </Routes>
    </div>
  );
}

export default App;
