import React, { useState } from "react";
import * as XLSX from "xlsx";
import axios from 'axios';
import { ExcelForm } from "./uploadModel";
import { FileUpload } from 'primereact/fileupload';



function Upload() {
  const [items, setItems] = useState([]); 
  const [Example, setExample] = useState(null);  
const data = ''
const [imageBase, setImageBase] = useState(null);  


  const customBase64Uploader = async (event) => {
    // convert file to base64 encoded
    const file = event.target.files[0];
    const objec = URL.createObjectURL(file);
    const encodedString = btoa(objec);
    axios.post(`http://localhost:8090/upload/souvenir/photos`,  {souvenirId: 1, image: encodedString})
    .then(res => {
  })  
}

const farjineLphoto = () => {
  
  axios.get(`http://localhost:8090/getsouvenir/photos?souvenirId=1`)
  .then(res => {
      console.log(res);
      const decodedString = atob(res.data[2].image);
      setImageBase(decodedString);
    });
};

  const readExcel = (file) => {
    const promise = new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsArrayBuffer(file);

      fileReader.onload = (e) => {
        const bufferArray = e.target.result;

        const wb = XLSX.read(bufferArray, { type: "buffer" });

        const wsname = wb.SheetNames[0];

        const ws = wb.Sheets[wsname];

        const data   = XLSX.utils.sheet_to_json(ws);
        console.log(data);
        if(data !== null ){
            axios.post(`http://localhost:8090/upload/market`, data)
            .then(res => {
                const persons = res.data;
                this.setState({ persons });
            })
        }
        resolve(data);
      };

      fileReader.onerror = (error) => {
        reject(error);
      };
    });

     
  };

  return (
    <div>
      <br />
      <label>Upload Market</label><br /><br />
      <input
        type="file"
        onChange={(e) => {
          const file = e.target.files[0];
          readExcel(file);
        }}
      />

    <br /><br /><br /><br />
    <label>Upload Souvenir</label>
    <br /><br />
    <input
        type="file"
        onChange={(e) => { 
          customBase64Uploader(e);
        }}
      />
         <br /><br /><br /><br />
      <input
        type="submit"
        onClick={(e) => { 
          farjineLphoto();
        }}
      />
      <br />
      {imageBase != null && <img src={imageBase} />}
    { Example !== null?<Example data={data} />:"" } 
    </div>
  );
}

export default Upload;