import { useEffect, useState, useRef} from "react";
import { TabMenu } from "primereact/tabmenu";
import axios from "axios";
import adminStyle from "./admin.module.css";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Button } from "primereact/button";
import "../../../node_modules/primeicons/primeicons.css";

const items = [
  { label: "notification", icon: "pi pi-fw pi-home" },
  { label: "upload", icon: "pi pi-fw pi-file", url: "/admin/upload" },
];

const Admin = () => {
  const deleteRef = useRef(null);
  const [activeIndex, setActiveIndex] = useState<number>();
  const [notifications, setNotifications] = useState([]);

  useEffect(() => {
    const intervalId = setInterval(() => {
      axios.get(`http://localhost:8090/notifications/get`).then((res) => {
        setNotifications(res.data);
      });
    }, 5000);
    return () => clearInterval(intervalId);
  }, []);

  const rowClickHandler = (e) => {
    if (deleteRef.current && deleteRef.current.contains(e.originalEvent.target)) {
      axios.delete(`http://localhost:8090/notifications/delete/${e.data.id}`).then((res) => {});
  }
};

  return (
    <div>
      <TabMenu
        model={items}
        activeIndex={activeIndex}
        onTabChange={(e) => setActiveIndex(e.index)}
      />

      <DataTable value={notifications} onRowClick={(e) => rowClickHandler(e)}>
        <Column field="id" header="Id" />
        <Column field="email" header="Email" />
        <Column field="type" header="Type" />
        <Column field="body.date" header="Date" />
        <Column field="body.comments" header="Comments" />
        <Column field="body.urgent" header="Urgent" />
        <Column body={<i className="pi pi-trash" ref={deleteRef}/>} />
      </DataTable>
    </div>
  );
};

export default Admin;
