import React, { useEffect, useState } from "react";
import { InputText } from "primereact/inputtext";
import welcomeStyle from "./welcome.module.css";
import { Button } from "primereact/button";
import { Dialog } from "primereact/dialog";
import { Calendar } from "primereact/calendar";
import { InputTextarea } from "primereact/inputtextarea";
import { Checkbox } from "primereact/checkbox";
import { Galleria } from "primereact/galleria";
import { useLocation } from "react-router-dom";
import { LoginCredentials } from "../models/models";
import moment from "moment";
import axios from "axios";

const Welcome = () => {
  const location = useLocation();
  const credentials = location.state as LoginCredentials;

  const [cleaningDialog, setCleaningDialog] = useState(false);
  const [maintenanceDialog, setMaintenanceDialog] = useState(false);

  const [cleaningDate, setCleaningDate] = useState(new Date());
  const [maintenanceDate, setMaintenanceDate] = useState(new Date());

  const [cleaningTextArea, setCleaningTextArea] = useState("");
  const [maintenanceTextArea, setMaintenanceTextArea] = useState("");

  const [cleaningPresent, setCleaningPresent] = useState(false);
  const [maintenancePresent, setMaintenancePresent] = useState(false);
  const [urgent, setUrgent] = useState(false);

  const [notification, setNotification] = useState({});

  const [images, setImages] = useState([

    {
      itemImageSrc: "images/home.jpg",
      alt: "home",
      thumbnailImageSrc: "images/home.jpg",
    },
    {
      itemImageSrc: "images/house.jpg",
      alt: "house",
      thumbnailImageSrc: "images/house.jpg",
    },
    {
      itemImageSrc: "images/login-bg.jpg",
      alt: "login-bg",
      thumbnailImageSrc: "images/login-bg.jpg",
    },
  ]);

  const responsiveOptions = [
    {
      breakpoint: "1024px",
      numVisible: 5,
    },
    {
      breakpoint: "768px",
      numVisible: 3,
    },
    {
      breakpoint: "560px",
      numVisible: 1,
    },
  ];

  useEffect(() => {
    if (Object.keys(notification).length > 0) {
      console.log(notification);
      axios
        .post(`http://localhost:8090/notifications/add`, notification)
        .then((res) => {});
    }
  }, [notification]);

  const itemTemplate = (item) => {
    return (
      <img
        src={item.itemImageSrc}
        alt={item.alt}
        style={{ width: "100%", display: "block" }}
      />
    );
  };

  const thumbnailTemplate = (item) => {
    return (
      <img
        src={item.thumbnailImageSrc}
        alt={item.alt}
        style={{ display: "block" }}
      />
    );
  };

  return (
    <React.Fragment>
      <div className="card">
        <Galleria
          value={images}
          responsiveOptions={responsiveOptions}
          numVisible={4}
          circular
          showItemNavigators
          showThumbnails={false}
          item={itemTemplate}
          thumbnail={thumbnailTemplate}
        />
      </div>
      <div className={welcomeStyle.bodyContainer}>
        <h1 style={{ fontWeight: "normal", display: "inline" }}>
          About the house:
          <br></br>
          this house is goooooood
        </h1>
      <div>
        <label
          style={{
            display: "block",
            marginBottom: ".5rem",
            marginTop: "1rem",
          }}
        >
          Wifi Code:{" "}
        </label>
        <InputText value="AirBnb123" disabled={true} />

        <Dialog
          header="Request Cleaning"
          visible={cleaningDialog}
          style={{ width: "fit-content" }}
          onHide={() => setCleaningDialog(false)}
        >
          <Calendar
            style={{ display: "block", marginBottom: ".5rem" }}
            inputStyle={{ width: "16.5rem" }}
            showTime
            hourFormat="12"
            value={cleaningDate}
            onChange={(e) =>
              setCleaningDate(new Date(e.value.toLocaleString()))
            }
          ></Calendar>
          <InputTextarea
            style={{ marginBottom: ".5rem", display: "block" }}
            placeholder="Comments Section"
            value={cleaningTextArea}
            onChange={(e) => setCleaningTextArea(e.target.value)}
          />
          <div style={{ marginBottom: ".5rem" }}>
            <Checkbox
              style={{ marginLeft: ".5rem" }}
              onChange={(e) => setCleaningPresent(e.checked)}
              checked={cleaningPresent}
            ></Checkbox>
            <label style={{ marginLeft: ".5rem" }} className="p-checkbox-label">
              Clean when I'm present
            </label>
          </div>
          <Button
            id={welcomeStyle.confirmButton}
            onClick={() => {
              setCleaningDialog(false);
              let options = {
                email: credentials.email,
                type: "cleaning",
                body: {
                  date: moment(cleaningDate).format("DD-MM-YYYY HH:mm"),
                  comments: cleaningTextArea,
                  present: cleaningPresent,
                  urgent: "false",
                },
              };
              setNotification(options);
            }}
            label="Confirm"
            className="p-button-raised p-button-rounded"
          />
        </Dialog>

        <Dialog
          header="Request Maintenance"
          visible={maintenanceDialog}
          style={{ width: "fit-content" }}
          onHide={() => setMaintenanceDialog(false)}
        >
          <Calendar
            disabled={urgent}
            style={{ display: "block", marginBottom: ".5rem" }}
            inputStyle={{ width: "16.5rem" }}
            showTime
            hourFormat="12"
            value={maintenanceDate}
            onChange={(e) =>
              setMaintenanceDate(new Date(e.value.toLocaleString()))
            }
          ></Calendar>
          <div style={{ marginBottom: ".5rem" }}>
            <Checkbox
              style={{ marginLeft: ".5rem" }}
              onChange={(e) => setUrgent(e.checked)}
              checked={urgent}
            ></Checkbox>
            <label style={{ marginLeft: ".5rem" }} className="p-checkbox-label">
              Urgent
            </label>
          </div>
          <InputTextarea
            style={{ marginBottom: ".5rem", display: "block" }}
            placeholder="Comments Section"
            value={maintenanceTextArea}
            onChange={(e) => setMaintenanceTextArea(e.target.value)}
          />
          <div style={{ marginBottom: ".5rem" }}>
            <Checkbox
              style={{ marginLeft: ".5rem" }}
              onChange={(e) => setMaintenancePresent(e.checked)}
              checked={maintenancePresent}
            ></Checkbox>
            <label style={{ marginLeft: ".5rem" }} className="p-checkbox-label">
              Fix when I'm present
            </label>
          </div>
          <Button
            id={welcomeStyle.confirmButton}
            onClick={() => {
              setMaintenanceDialog(false);
              let options = {
                email: credentials.email,
                type: "maintenance",
                body: {
                  date: moment(maintenanceDate).format("DD-MM-YYYY HH:mm"),
                  comments: maintenanceTextArea,
                  present: maintenancePresent,
                  urgent: urgent ? "true" : "false",
                },
              };
              setNotification(options);
            }}
            label="Confirm"
            className="p-button-raised p-button-rounded"
          />
        </Dialog>
      </div>
      <div className={welcomeStyle.buttonGroup}>
        <Button
          id={welcomeStyle.button1}
          onClick={() => setCleaningDialog(true)}
          label="Request Cleaning"
          className="p-button-raised p-button-rounded"
          style={{ margin: "1rem" }}
        />
        <Button
          id={welcomeStyle.button2}
          onClick={() => setMaintenanceDialog(true)}
          label="Request Maintenance"
          className="p-button-raised p-button-rounded"
          style={{ margin: "1rem" }}
        />
      </div>
    </div>
    </React.Fragment>
  );
};

export default Welcome;
